<template>
  <div class="update">
      <field
        v-model="value"
        clearable
        :error="error"
        @keyup.enter.native="updateUser"
      />
  </div>
</template>

<script>
import { debounce } from "@/utils";
import { Field } from "vant";
export default {
  components: { Field },
  data() {
    return {
      error: false,
      value: this.$route.query.value,
    };
  },
  watch: {
    value() {
      this.validate();
    }
  },
  mounted() {
    this.debounce = debounce(this.validate);
    this.$bus.$on("updateUser", () => {
      this.updateUser();
    });
  },
  destroyed() {
    this.$bus.$off("updateUser");
  },
  methods: {
    updateUser() {
      this.$store
        .dispatch("updateUserInfo", [this.$route.query.key, this.value])
        .then(() => {
          this.back();
        })
        .catch(() => {});
    },
    validate() {
      if (this.value === "") {
        this.error = true;
      } else {
        this.error = false;
      }
    }
  }
};
</script>
